.game-page {
  background-image: url("fondo.60c87ae2.png");
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (min-width: 768px) {
  .game-page {
    flex-direction: column;
    gap: 130px;
    display: flex;
  }
}

.countdown {
  font-family: Special Elite, cursive;
  font-size: 100px;
  font-weight: 700;
}

.countdown-container {
  height: 240px;
  width: 240px;
  background: none;
  border: 23px solid #000;
  border-radius: 120px;
  justify-content: center;
  align-items: center;
  margin: 125px 65px;
  display: flex;
}

.imagen-container {
  gap: 11px;
  display: flex;
}

.zoom {
  width: 100%;
  justify-content: center;
  margin: 0 auto;
  padding: 10px;
  transition: transform .2s;
  display: flex;
}

.zoom:hover {
  transform: scale(1.2);
}

